<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">报修详情</p>
        </div>
        <div class="content c666 f28" style="padding:1.3rem 0.3rem 1.7rem;line-height: 1.1rem;">
            <p>设备类别：<span class="c333">{{data.labelName}}</span></p>
            <p>所在班级：<span class="c333">{{data.classesName}}</span></p>
            <p>报修时间：<span class="c333">{{data.createTime}}</span></p>
            <p>报修原因：<span class="c333">{{data.repairDesc}}</span></p>
        </div>
        <div class="centerBtn f26 cfff text-center" @click="issue">立即处理</div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            data:{
                name:'桌子',
                class:'3 年 2 班',
                time:'2018-06-09  14:14:58',
                reason:'桌子腿有断裂痕迹'
            }
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        not(){
            this.active=true
            this.active1=false
        },
        yes(){
            this.active=false
            this.active1=true
        },
        lookDetail(index){
            var list=this.list;
            list[index].isSee=true;
            this.list=list;
            this.$router.push({path:'/leaveDetail',query:{active:this.active}})
        },
        issue(){

        },
        getEquipmentDesc(){//详情
            this.$http({url:this.$api.message.getEquipmentDesc},{id:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data = data.data;
                }
            })
        }
    },
    created() {
        this.getEquipmentDesc();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
</style>




